import Vue from 'vue'
import VueRouter from 'vue-router'
import theme from '../themes/lbm'
import Welcome from '../pages/home/Welcome'

Vue.use(VueRouter)

const BlankRouter = {
  template: '<router-view />'
}

const routes = [
  {
    path: '/',
    component: theme.layouts.RouterBaseLayout,
    children: [
      {
        path: '',
        redirect: '/home/dashboard/analysis'
      },
      {
        path: 'welcome',
        component: Welcome
      },
      {
        path: 'home',
        component: BlankRouter,
        children: [
          {
            path: 'dashboard/monitor',
            name: 'monitor-dashboard',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/dashboard/Monitor')
          },
          {
            path: 'dashboard/work',
            name: 'work-dashboard',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/dashboard/Work')
          },
          {
            path: 'dashboard/analysis',
            name: 'analysis-dashboard',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/dashboard/Analysis')
          },
          {
            path: 'list/normal',
            name: 'normal-list',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/list/NormalList')
          },
          {
            path: 'list/content',
            name: 'content-list',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/list/ContentList')
          },
          {
            path: 'list/table',
            name: 'table-list',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/list/TableList')
          },
          {
            path: 'list/table-group',
            name: 'table-group-list',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/list/TableGroupList')
          },
          {
            path: 'list/card',
            name: 'card-list',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/list/CardList')
          },
          {
            path: 'list/session',
            name: 'session-list',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/list/SessionList')
          },
          {
            path: 'list/timeline',
            name: 'timeline-list',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/list/TimelineList')
          },
          {
            path: 'list/map',
            name: 'map-list',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/list/MapList')
          },
          {
            path: 'profile/base',
            name: 'base-profile',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/profile/BaseProfile')
          },
          {
            path: 'profile/overview',
            name: 'overview-profile',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/profile/OverviewProfile')
          },
          {
            path: 'profile/order',
            name: 'order-profile',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/profile/OrderProfile')
          },
          {
            path: 'profile/weekly',
            name: 'weekly-profile',
            component: () => import(/* webpackChunkName: "normal-list" */ '../pages/home/profile/WeeklyProfile')
          }
        ]
      },
      {
        path: 'products',
        name: 'Products',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "product-list" */ '../pages/products/ProductList')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
