import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import theme from './themes/lbm'
import './registerServiceWorker'

// Antd
import Antd from 'ant-design-vue'
import './themes/lbm/less/index.less'
import zhCN from 'ant-design-vue/lib/locale-provider/zh_CN'

// Moment
import moment from 'moment'
import 'moment/locale/zh-cn'
moment.locale('zh-cn')

Vue.mixin({
  methods: {
    moment
  }
})
Vue.config.productionTip = false
Vue.use(theme)
Vue.use(Antd)

new Vue({
  router,
  store,
  components: { App },
  template: '<a-locale-provider :locale="zhCN"><App/></a-locale-provider>',
  data () {
    return {
      zhCN
    }
  }
}).$mount('#app')
