<template>
  <a-layout>
    <a-layout-sider width="70px" :collapsed="true" :style="{ overflow: 'auto', height: '100vh', position: 'fixed', left: 0 }">
      <div class="logo" />
      <a-menu
        :defaultSelectedKeys="[activeMenuKey]"
        mode="inline"
        theme="dark"
        @click="handleClick"
      >
        <a-menu-item v-for="m in menus" :key="m.res_key">
          <a-icon :type="m.icon" />
          <span>{{m.res_name}}</span>
        </a-menu-item>
      </a-menu>
    </a-layout-sider>
    <a-layout :style="{ marginLeft: '80px' }">
      <a-layout-sider  style="background: #fff; padding: 16px 4px 16px 16px; box-shadow: 0px 0px 24px 0px rgba(68, 94, 167, 0.13); overflow-y: auto; position: fixed; height: 100vh;" width="180px">
        <h2>系统功能</h2>
        <a-menu
          @click="handleSubClick"
          :selectedKeys="[$route.path]"
          style="width: 100%"
          mode="inline">
          <a-menu-item-group v-for="sm in subMenus" :key="sm.res_key">
            <template v-slot:title>
              <h4>{{sm.res_name}}</h4>
            </template>
            <template v-if="sm.child">
              <a-menu-item
                v-for="ssm in sm.child"
                :key="`/${activeMenuKey}/${sm.res_key}/${ssm.res_key}`">
                {{ssm.res_name}}
              </a-menu-item>
            </template>
          </a-menu-item-group>
        </a-menu>
      </a-layout-sider>
      <a-layout :style="{ marginLeft: '180px'}">
        <!-- <a-layout-header style="background: #fff; padding: 0">Header</a-layout-header> -->
        <a-layout-content>
          <keep-alive>
            <router-view />
          </keep-alive>
        </a-layout-content>
        <a-layout-footer style="padding: 16px; text-align: right;"> 2020 © xAtlas</a-layout-footer>
      </a-layout>
    </a-layout>
  </a-layout>
</template>

<script>
export default {
  props: {
    resources: Array
  },
  data () {
    return {
      current: '1',
      theme: 'dark',
      activeMenuKey: this.$store.state.resources.menus[0].res_key
    }
  },
  computed: {
    menus () {
      return this.$store.state.resources.menus
    },
    subMenus () {
      return this.menus.find(m => m.res_key === this.activeMenuKey).child || []
    }
  },
  methods: {
    handleClick (e) {
      this.activeMenuKey = e.key
    },
    handleSubClick (e) {
      this.$router.push({ path: e.key })
    }
  }
}
</script>

