  h2 {
    padding: 8px 16px 0 16px;
  }
  h4 {
    margin: 0.5em 0 0 0;
  }
  .logo {
    height: 32px;
    background: rgba(255, 255, 255, 0.2);
    margin: 24px;
  }
  .ant-menu-vertical > .ant-menu-item {
    margin-top: 16px;
  }
  .ant-menu-inline-collapsed > .ant-menu-item .anticon {
    font-size: 20px;
  }
  .ant-menu-inline, .ant-menu-vertical, .ant-menu-vertical-left {
    border-right: 0;
  }
</style>
